import React from "react"
import PropTypes from "prop-types"
import BodyClassName from "react-body-classname"
import Helmet from "react-helmet"
import { useCookies } from "react-cookie"
import "./reset.css"
import Header from "../components/header/header"
import Footer from "../components/footer/footer"
import Styles from "./layout.module.css"
import ThemeSwitch from "../components/themeswitch/themeswitch"

const Layout = props => {
    const cookieConfig = {
        name: "theme",
        path: "/",
        sameSite: "lax",
    }
    const [cookie, setCookie] = useCookies([cookieConfig.name])
    return (
        <BodyClassName
            className={`${Styles.layoutBody} theme--${
                cookie.theme === "true" ? "light" : "dark"
            }`}
        >
            <div style={{ opacity: props.home && !props.visibility ? 0 : 1 }}>
                <Helmet>
                    <title>James Maclennan - Front End Web Developer</title>
                    <link
                        href="https://fonts.googleapis.com/css?family=Roboto|Lato"
                        rel="stylesheet"
                    />
                </Helmet>
                <Header>
                    <ThemeSwitch {...{ cookieConfig, cookie, setCookie }} />
                </Header>
                <main>{props.children}</main>
                <Footer />
            </div>
        </BodyClassName>
    )
}

Layout.defaultProps = {
    home: false,
    visibility: true,
}

Layout.propTypes = {
    home: PropTypes.bool,
    visiblity: PropTypes.bool,
    children: PropTypes.node.isRequired,
}

export default Layout
